import Main from '@/components/main';

/**
 * iview-admin中meta除了原生参数外可配置的参数:
 * meta: {
 *  title: { String|Number|Function }
 *         显示在侧边栏、面包屑和标签栏的文字
 *         使用'{{ 多语言字段 }}'形式结合多语言使用，例子看多语言的路由配置;
 *         可以传入一个回调函数，参数是当前路由对象，例子看动态路由和带参路由
 *  hideInMenu: (false) 设为true后在左侧菜单不会显示该页面选项
 *  notCache: (false) 设为true后页面不会缓存
 *  access: (null) 可访问该页面的权限数组，当前路由设置的权限会影响子路由
 *  icon: (-) 该页面在左侧菜单、面包屑和标签导航处显示的图标，如果是自定义图标，需要在图标名称前加下划线'_'
 *  beforeCloseName: (-) 设置该字段，则在关闭当前tab页时会去'@/router/before-close.js'里寻找该字段名对应的方法，作为关闭前的钩子函数
 * }
 */

export default [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: 'Login - 登录',
            hideInMenu: true
        },
        component: () => import('@/nmedia/views/login.vue')
    },
    {
        path: '/forwardTemp',
        name: 'ForwardTemp',
        meta: {
            hideInMenu: true,
            title: '跳转绘制模板',
            access: ['m:temp:build:for', '*']
        },
        component: () => import('@/nmedia/views/temp/index.vue')
    },
    {
        path: '/',
        name: '_home',
        redirect: '/home',
        component: Main,
        meta: {
            hideInMenu: true,
            notCache: true
        },
        children: [
            {
                path: '/home',
                name: 'home',
                meta: {
                    title: '首页',
                    notCache: true,
                    icon: 'md-home'
                },
                component: () => import('@/nmedia/views/single-page/home.vue')
            }
        ]
    },
    {
        path: '/template',
        name: 'Template',
        meta: {
            title: '模版管理',
            access: ['m:temp', '*']
        },
        component: Main,
        children: [
            {
                path: 'store',
                name: 'TempStore',
                meta: {
                    icon: 'md-apps',
                    title: '模版库',
                    access: ['m:temp:store', '*']
                },
                component: () => import('@/nmedia/views/temp/storeforward.vue')
            },
            {
                path: '',
                name: 'SelectTemplate',
                meta: {
                    hideInMenu: true,
                    title: '使用模板',
                    access: ['m:temp:build:select', '*'],
                    href: '#/forwardTemp'
                }
            }]
    },
    {
        path: '/content',
        name: 'Content',
        meta: {
            title: '活动管理',
            access: ['m:content', '*']
        },
        component: Main,
        children: [{
            path: 'publish',
            name: 'ContentPublish',
            meta: {
                icon: 'md-apps',
                title: '发布',
                access: ['m:content:pub', '*']
            },
            component: () => import('@/nmedia/views/content/pub.vue')
        },
        {
            path: 'list',
            name: 'PublishList',
            meta: {
                icon: 'md-apps',
                title: '活动列表',
                access: ['m:content:list', '*']
            },
            component: () => import('@/nmedia/views/content/list.vue')
        }]
    }, {
        path: '/product',
        name: 'Product',
        meta: {
            title: '产品管理',
            access: ['m:product', '*']
        },
        component: Main,
        children: [{
            path: 'addsubproduct',
            name: 'AddSubProduct',
            meta: {
                title: '新增子产品',
                notTag: true,
                notCache: true,
                hideInMenu: true,
                access: ['m:product:sub:add', '*']
            },
            component: () => import('@/nmedia/views/product/addsubproduct.vue')
        }, {
            path: 'subproductlist',
            name: 'SubProductList',
            meta: {
                title: '子产品列表',
                notTag: true,
                notCache: true,
                hideInMenu: true,
                access: ['m:product:sub:list', '*']
            },
            component: () => import('@/nmedia/views/product/subProductList.vue')
        }, {
            path: 'productlist',
            name: 'ProductList',
            meta: {
                icon: 'md-apps',
                title: '产品列表',
                access: ['m:product:list', '*']
            },
            component: () => import('@/nmedia/views/product/productList.vue')
        }]
    },
    {
        path: '/role',
        name: 'Role',
        meta: {
            title: '角色管理',
            access: ['m:nmedia:perm:role', '*']
        },
        component: Main,
        children: [
            {
                path: 'audit',
                name: 'RoleAudit',
                meta: {
                    icon: 'md-apps',
                    title: '待审核列表',
                    access: ['m:nmedia:perm:role:audit:list', '*']
                },
                component: () => import('@/nmedia/views/role/aud.vue')
            },
            {
                path: 'allUserList',
                name: 'RoleAllUserList',
                meta: {
                    icon: 'md-apps',
                    title: '全部用户列表',
                    access: ['m:nmedia:perm:role:list', '*']
                },
                component: () => import('@/nmedia/views/role/alluser.vue')
            }]
    },
    {
        path: '/credit',
        name: 'Credit',
        meta: {
            title: '积分管理',
            access: ['m:nmedia:credit', '*']
        },
        component: Main,
        children: [
            {
                path: 'creditList',
                name: 'CreditList',
                meta: {
                    icon: 'md-apps',
                    title: '积分列表',
                    access: ['m:nmedia:credit:list', '*']
                },
                component: () => import('@/nmedia/views/credit/creditList.vue')
            }
        ]
    },
    {
        path: '/401',
        name: 'error_401',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/401.vue')
    },
    {
        path: '/500',
        name: 'error_500',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/500.vue')
    },
    {
        path: '*',
        name: 'error_404',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/404.vue')
    }
];
