import { setToken, getToken } from '@/libs/util';

export default {
    state: {
        username: '',
        userId: '',
        avatorImgPath: '',
        token: getToken(),
        access: '',
        info: {},
        session: ''
    },
    mutations: {
        setAvator (state, avatorPath) {
            state.avatorImgPath = avatorPath;
        },
        setUserId (state, id) {
            state.userId = id;
        },
        setUserName (state, name) {
            state.userName = name;
        },
        setAccess (state, access) {
            state.access = access;
        },
        setToken (state, token) {
            state.token = token;
            setToken(token);
        },
        setInfo (state, info) {
            state.info = info;
        },
        setSession (state, session) {
            state.session = session;
        }
    },
    actions: {
        // 登录
        async handleLogin ({ commit }, { username, password }) {
            const result = await this._vm.post('/login', {
                username,
                password
            });
            if (result.success) {
                commit('setToken', 'login');
            }
            return result;
        },
        // 退出登录
        async handleLogOut ({ state, commit }) {
            commit('setToken', '');
            commit('setSession', '');
            commit('setAccess', []);
        },
        // 获取用户相关信息
        async getUserInfo ({ state, commit }) {
            const result = await this._vm.get('/info', {}, { showError: false });
            if (!result.success) {
                commit('setToken', '');
                commit('setAccess', []);
                commit('setSession', '');
                return false;
            }
            const superAdmin = result.value.roles.some(v => v.type === 0);
            const access = superAdmin ? ['*'] : result.value.permissions.filter(v => v.type === 1).map(v => v.value);
            commit('setAccess', access);
            commit('setInfo', result.value);
            commit('setSession', result.data.token);
            return {
                user_name: result.value.name,
                access,
                session: result.data.token
            };
        },
        async getAccountInfo () {
            const result = await this._vm.get('/api/ep/account/get');
            if (!result.success) {
                return null;
            }
            return result.value;
        }
    }
};
