import axios from 'axios';
import qs from 'qs';
import { Spin, LoadingBar, Notice } from 'iview';
import merge from 'lodash.merge';
import router from '@/fx/router';
import HttpError from './HttpError';
import { platform } from './common';

const DEFAULT_CONFIG = { showError: true, loading: false };

axios.defaults.baseURL = $config.SERVER_ADDRESS;
axios.defaults.withCredentials = true;
axios.defaults.headers['x-platform'] = platform;
console.log('BASE_URL:', axios.defaults.baseURL);
axios.interceptors.request.use(config => {
    const query = qs.parse(window.location.search, {
        ignoreQueryPrefix: true
    });
    const { hash } = location;
    if (hash) {
        const index = hash.indexOf('?');
        if (index > -1) {
            const hashQuery = qs.parse(hash.substring(index), {
                ignoreQueryPrefix: true
            });
            query.openid = query.openid || hashQuery.openid;
            query._s = query._s || hashQuery._s;
        }
    }
    return merge(
        {
            withCredentials: true,
            headers: {
                'x-wx-openid': query.openid,
                'x-wx-sign': query._s
            }
        },
        config
    );
});
axios.interceptors.response.use(async (res) => {
    const data = { success: false, ...res.data };
    if (!data.success) {
        throw new HttpError(data.msg || '操作失败!', res, data);
    }
    return data;
});

export const fetch = async (url, config = DEFAULT_CONFIG) => {
    const { showError, loading } = merge({ ...DEFAULT_CONFIG }, config);
    try {
        if (!url) {
            throw new Error('url must be not null');
        }
        LoadingBar.start();
        if (loading !== false) {
            Spin.show();
        }
        const response = await axios(url, config);
        LoadingBar.finish();
        return response;
    } catch (err) {
        console.warn(err);
        LoadingBar.error();
        if (showError) {
            Notice.error({ title: err.message });
        }
        if (
            (err.code && err.code === 403) ||
            (err.response && err.response.status === 403)
        ) {
            router.replace('/login');
        }
        return { success: false };
    } finally {
        Spin.hide();
    }
};

export const get = async (url, params, config) => fetch(url, merge({ method: 'get', params }, DEFAULT_CONFIG, config));

export const post = async (url, data, config) => fetch(url, merge({ method: 'post', data }, DEFAULT_CONFIG, config));

export default {
    get, post, fetch
};
