import Vue from 'vue';
import Router from 'vue-router';
import routes, { FX } from '@/router';
import store from '@/fx/store';
import iView from 'iview';
import { getToken, canTurnTo } from '@/libs/util';
import config from '@/config';
const { homeName } = config;

Vue.use(Router);
const router = new Router({
    routes: FX
});
const LOGIN_PAGE_NAME = 'login';

const turnTo = (to, access, next) => {
    if (canTurnTo(to.name, access, FX)) next(); // 有权限，可访问
    else next({ replace: true, name: 'error_401' }); // 无权限，重定向到401页面
};

router.beforeEach(async (to, from, next) => {
    iView.LoadingBar.start();
    const token = getToken();
    if (!token && to.name !== LOGIN_PAGE_NAME) {
        // 未登录且要跳转的页面不是登录页
        next({
            name: LOGIN_PAGE_NAME, // 跳转到登录页
            params: to
        });
    } else if (!token && to.name === LOGIN_PAGE_NAME) {
        // 未登陆且要跳转的页面是登录页
        next(); // 跳转
    } else if (token && to.name === LOGIN_PAGE_NAME) {
        // 已登录且要跳转的页面是登录页
        next({
            name: homeName // 跳转到homeName页
        });
    } else {
        let { session } = store.state.user;
        if (!session) {
            const info = await store.dispatch('getUserInfo');
            if (info) {
                ({ session } = info);
            }
        }
        if (!session) {
            next({
                name: LOGIN_PAGE_NAME, // 跳转到登录页
                params: to
            });
            return;
        }
        const { access } = store.state.user;
        turnTo(to, access, next);
    }
});

router.afterEach(to => {
    iView.LoadingBar.finish();
    window.scrollTo(0, 0);
});

export default router;

export const menus = () => routes.map(router => ({ name: router.name, type: 0, children: _menus(router.routers) }));

const _menus = (rs = routes) => rs.filter(r => r.meta && r.meta.access && r.meta.access.some(a => a !== '*')).map(r => {
    const accessArray = r.meta.access.filter(a => a !== '*');
    const permission = { name: r.meta.title, value: accessArray[0], type: 1 };
    if (r.children && r.children.length) {
        permission.children = _menus(r.children);
    }
    return permission;
});
