import Main from '@/components/main';

/**
 * iview-admin中meta除了原生参数外可配置的参数:
 * meta: {
 *  title: { String|Number|Function }
 *         显示在侧边栏、面包屑和标签栏的文字
 *         使用'{{ 多语言字段 }}'形式结合多语言使用，例子看多语言的路由配置;
 *         可以传入一个回调函数，参数是当前路由对象，例子看动态路由和带参路由
 *  hideInMenu: (false) 设为true后在左侧菜单不会显示该页面选项
 *  notCache: (false) 设为true后页面不会缓存
 *  access: (null) 可访问该页面的权限数组，当前路由设置的权限会影响子路由
 *  icon: (-) 该页面在左侧菜单、面包屑和标签导航处显示的图标，如果是自定义图标，需要在图标名称前加下划线'_'
 *  beforeCloseName: (-) 设置该字段，则在关闭当前tab页时会去'@/router/before-close.js'里寻找该字段名对应的方法，作为关闭前的钩子函数
 * }
 */

export default [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: 'Login - 登录',
            hideInMenu: true
        },
        component: () => import('@/fx/views/login.vue')
    },
    {
        path: '/',
        name: '_home',
        redirect: '/home',
        component: Main,
        meta: {
            hideInMenu: true,
            notCache: true
        },
        children: [
            {
                path: '/home',
                name: 'home',
                meta: {
                    hideInMenu: true,
                    title: '首页',
                    notCache: true,
                    icon: 'md-home'
                },
                component: () => import('@/fx/views/single-page/home.vue')
            }
        ]
    },
    {
        path: '',
        name: 'doc',
        meta: {
            title: '文档',
            href: 'https://lison16.github.io/iview-admin-doc/#/',
            icon: 'ios-book'
        }
    },
    {
        path: '/permission',
        name: 'permission',
        component: Main,
        meta: {
            title: '权限管理',
            access: ['m:perm', '*']
        },
        children: [
            {
                path: 'user',
                name: 'User',
                meta: {
                    icon: 'md-apps',
                    title: '用户管理',
                    access: ['m:perm:user', '*']
                },
                component: () => import('@/fx/views/permission/user.vue')
            },
            {
                path: 'role',
                name: 'Role',
                meta: {
                    icon: 'md-apps',
                    title: '角色管理',
                    access: ['m:perm:role', '*']
                },
                component: () => import('@/fx/views/permission/role.vue')
            },
            {
                path: 'role/bind',
                name: 'RoleBindPermission',
                meta: {
                    title: '绑定角色权限',
                    notTag: true,
                    notCache: true,
                    hideInMenu: true,
                    access: ['m:perm:role:bind', '*']
                },
                component: () => import('@/fx/views/permission/bind.vue')
            },
            {
                path: 'index',
                name: 'Permission',
                meta: {
                    title: '权限资源',
                    access: ['m:perm:resource', '*']
                },
                component: () =>
                    import('@/fx/views/permission/permission.vue')
            }
        ]
    },
    {
        path: '/refund/rule',
        name: 'Refund',
        component: Main,
        meta: {
            title: '退货规则管理',
            access: ['*', 'm:fx:refund:rule']
        },
        children: [
            {
                path: 'list',
                name: 'RefundRuleList',
                meta: {
                    title: '退货规则列表',
                    access: ['*', 'm:fx:refund:rule:list']
                },
                component: () =>
                import('@/fx/views/refundRule/refundRule-list.vue')
            },
            {
                path: 'add',
                name: 'RefundRulesAdd',
                meta: {
                    title: '添加退货规则',
                    access: ['*', 'm:fx:refund:rule:add'],
                    notCache: true
                },
                component: () =>
                import('@/fx/views/refundRule/rule-add.vue')
            },
            {
                path: 'add',
                name: 'RefundRuleUpdate',
                meta: {
                    title: '修改退货规则',
                    notTag: true,
                    notCache: true,
                    hideInMenu: true,
                    access: ['*', 'm:fx:refund:rule:update']
                },
                component: () =>
                import('@/fx/views/refundRule/rule-update.vue')
            }

        ]
    },
    {
        path: '/distribute',
        name: 'Distribute',
        component: Main,
        meta: {
            title: '产品分销',
            access: ['*', 'm:fx:distribute']
        },
        children: [
            {
                path: 'scenery',
                name: 'SceneryDist',
                meta: {
                    title: '景点分销管理',
                    access: ['*', 'm:fx:distribute:scenery']
                },
                component: () =>
                import('@/fx/views/distribute/distribute-scenery.vue')
            },
            {
                path: 'group',
                name: 'DistGroup',
                meta: {
                    title: '渠道管理',
                    access: ['*', 'm:fx:distribute:group']
                },
                component: () =>
                import('@/fx/views/distribute/distribute-group.vue')
            },
            {
                path: 'group/ep/up',
                name: 'DistUpEp',
                meta: {
                    title: '分组上架企业',
                    notTag: true,
                    notCache: true,
                    hideInMenu: true,
                    access: ['m:fx:distribute:ep:up', '*']
                },
                component: () => import('@/fx/views/distribute/distribute-upEp.vue')
            },
            {
                path: 'group/sub/up',
                name: 'DistUpSubPro',
                meta: {
                    title: '分组上架子产品',
                    notTag: true,
                    notCache: true,
                    hideInMenu: true,
                    access: ['m:fx:distribute:sub:up', '*']
                },
                component: () => import('@/fx/views/distribute/distribute-upSubPro.vue')
            },
            {
                path: 'group/sub/down',
                name: 'DistDownSubPro',
                meta: {
                    title: '分组下架子产品',
                    notTag: true,
                    notCache: true,
                    hideInMenu: true,
                    access: ['m:fx:distribute:sub:down', '*']
                },
                component: () => import('@/fx/views/distribute/distribute-downSubPro.vue')
            },
            {
                path: 'hotel',
                name: 'HotelDist',
                meta: {
                    title: '酒店分销管理',
                    access: ['*', 'm:fx:distribute:hotel']
                },
                component: () =>
                import('@/fx/views/distribute/distribute-scenery.vue')
            }
        ]
    },
    {
        path: '/product',
        name: 'Product',
        component: Main,
        meta: {
            title: '产品管理',
            access: ['*', 'm:fx:product']
        },
        children: [
            {
                path: 'scenery',
                name: 'Scenery',
                meta: {
                    title: '景区管理',
                    access: ['*', 'm:fx:product:scenery']
                },
                component: () =>
                import('@/fx/views/product/scenery.vue')
            },
            {
                path: 'ticket/add',
                name: 'TicketAdd',
                meta: {
                    title: '创建景区子产品',
                    notTag: true,
                    hideInMenu: true,
                    access: ['*', 'm:fx:product:scenery:add']
                },
                component: () => import('@/fx/views/product/ticket-add.vue')
            },
            {
                path: 'ticket/update',
                name: 'TicketUpdate',
                meta: {
                    title: '修改景区子产品',
                    notTag: true,
                    hideInMenu: true,
                    access: ['*', 'm:fx:product:scenery:update']
                },
                component: () => import('@/fx/views/product/ticket-update.vue')
            },
            {
                path: 'ticket/plan/add',
                name: 'TicketPlan',
                meta: {
                    title: '创建景区产品销售计划',
                    notTag: true,
                    hideInMenu: true,
                    access: ['*', 'm:fx:product:scenery:plan']
                },
                component: () => import('@/fx/views/product/ticket-plan.vue')
            },
            {
                path: 'ticket/plan/list',
                name: 'PlanList',
                meta: {
                    title: '查看景区产品销售计划',
                    notTag: true,
                    hideInMenu: true,
                    access: ['*', 'm:fx:product:scenery:plan:list']
                },
                component: () => import('@/fx/views/product/ticket-plan-list.vue')
            },
            {
                path: 'hotel',
                name: 'Hotel',
                meta: {
                    title: '酒店管理',
                    access: ['*', 'm:fx:product:hotel']
                },
                component: () => import('@/fx/views/product/hotel.vue')
            }

        ]

    },
    {
        path: '/platform',
        name: 'platform',
        component: Main,
        meta: {
            title: '平台商管理',
            access: ['m:platform', '*']
        },
        children: [{
            path: 'index',
            name: 'PlatformIndex',
            meta: {
                icon: 'md-apps',
                title: '平台商列表',
                access: ['m:platform:index', '*']
            },
            component: () => import('@/fx/views/platform/index.vue')
        }]
    },
    {
        path: '/ep',
        name: 'ep',
        component: Main,
        meta: {
            title: '企业管理',
            access: ['m:ep', '*']
        },
        children: [{
            path: 'index',
            name: 'EpIndex',
            meta: {
                icon: 'md-apps',
                title: '企业列表',
                access: ['m:ep:index', '*']
            },
            component: () => import('@/fx/views/ep/index.vue')
        }]
    },
    {
        path: '/setting',
        name: 'setting',
        component: Main,
        meta: {
            title: '系统配置',
            access: ['m:setting', '*']
        },
        children: [{
            path: 'payment-config',
            name: 'PaymentConfig',
            meta: {
                icon: 'md-apps',
                title: '收款方式',
                access: ['m:setting:payment-config', '*']
            },
            component: () => import('@/fx/views/setting/payment-config.vue')
        }, {
            path: 'sms-template',
            name: 'SmsTemplate',
            meta: {
                icon: 'md-apps',
                title: '短信模板',
                access: ['m:setting:sms-template', '*']
            },
            component: () => import('@/fx/views/setting/sms-template.vue')
        }, {
            path: 'supplier-ticketing',
            name: 'SupplierTicketing',
            meta: {
                icon: 'md-apps',
                title: '供应商&票务',
                access: ['m:setting:supplier-ticketing', '*']
            },
            component: () => import('@/fx/views/setting/supplier-ticketing-config.vue')
        }]
    },
    {
        path: '/booking',
        name: 'booking',
        component: Main,
        meta: {
            title: '产品预订',
            access: ['m:fx:booking', '*']
        },
        children: [{
            path: 'scenic',
            name: 'BookingScenic',
            meta: {
                icon: 'md-apps',
                title: '景点预订',
                access: ['m:fx:booking:scenic', '*']
            },
            component: () => import('@/fx/views/booking/scenic.vue')
        }]
    },
    {
        path: '/order',
        name: 'order',
        component: Main,
        meta: {
            title: '订单中心',
            access: ['m:fx:order', '*']
        },
        children: [{
            path: 'list',
            name: 'OrderList',
            meta: {
                icon: 'md-apps',
                title: '订单列表',
                access: ['m:fx:order:list', '*']
            },
            component: () => import('@/fx/views/order/list.vue')
        }]
    },
    {
        path: '/finance',
        name: 'finance',
        component: Main,
        meta: {
            title: '财务中心',
            access: ['m:fx:finance', '*']
        },
        children: [{
            path: 'my',
            name: 'FinanceMy',
            meta: {
                icon: 'md-apps',
                title: '我的账户',
                access: ['m:fx:finance:my', '*']
            },
            component: () => import('@/fx/views/finance/my.vue')
        }, {
            path: 'ep',
            name: 'FinanceEp',
            meta: {
                icon: 'md-apps',
                title: '企业账户管理',
                access: ['m:fx:finance:ep', '*']
            },
            component: () => import('@/fx/views/finance/ep.vue')
        }]
    },
    {
        path: '/401',
        name: 'error_401',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/401.vue')
    },
    {
        path: '/500',
        name: 'error_500',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/500.vue')
    },
    {
        path: '*',
        name: 'error_404',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/404.vue')
    }
];
