'use strict';

export function getAttribute (dic, prop, value, isArray = false) {
    const result = [];
    for (const item of dic) {
        if (item[prop] === value) {
            if (!isArray) return item;
            result.push(item);
        }
    }
    return isArray ? result : null;
}

export function opt (dic, value, defVal = {}, prop = 'id') {
    const v = getAttribute(dic, prop, value, false);
    return v || defVal;
}

