import Vue from 'vue';
import store from '@/fx/store';
export const permission = (el, binding) => {
    if (typeof binding.value !== 'string') {
        console.warn('权限值必须为字符串,建议`b:`前缀', el);
        return;
    }
    const permissions = store.state.user.info?.permissions || [];
    const check = permissions.some(p => p.value === '*' || p.value === binding.value);
    if (!check) {
        el.setAttribute('disabled', true);
    }
    if (binding.modifiers.show) {
        el.style.display = check ? '' : 'none';
    }
};
export default {
    install () {
        Vue.directive('permission', permission);
    }
};
