import Cookies from 'js-cookie';
// cookie保存的天数
import config from '@/config';
import { hasOneOf, objEqual } from '@/libs/tools';

export const TOKEN_KEY = 'token';

export const setToken = (token) => {
    Cookies.set(TOKEN_KEY, token, { expires: config.cookieExpires || 1 });
};

export const getToken = () => {
    const token = Cookies.get(TOKEN_KEY);
    if (token) return token;
    else return false;
};

export const hasChild = (item) => !!item?.children?.length;

const showThisMenuEle = (item, access) => item.meta?.access?.length ? hasOneOf(item.meta.access, access) : true;
/**
 * @param {Array} list 通过路由列表得到菜单列表
 * @returns {Array}
 */
export const getMenuByRouter = (list = [], access) => list.filter(item => !item.meta || (item.meta && !item.meta.hideInMenu)).map(item => ({
    icon: item.meta?.icon || '',
    name: item.name,
    meta: item.meta,
    children: (hasChild(item) || item.meta?.showAlways) && showThisMenuEle(item, access) ? getMenuByRouter(item.children, access) : [],
    href: item.meta?.href
})).filter(item => showThisMenuEle(item, access));

/**
 * @param {Array} routeMetched 当前路由metched
 * @returns {Array}
 */
export const getBreadCrumbList = (route, homeRoute) => {
    const homeItem = { ...homeRoute, icon: homeRoute.meta?.icon };
    const routeMetched = route.matched;
    if (routeMetched.some(item => item.name === homeRoute.name)) return [homeItem];
    const res = routeMetched.filter(item => !item.meta?.hide).map(item => ({
        icon: item.meta?.icon || '',
        name: item.name,
        meta: { ...item.meta, title: typeof item.meta?.title === 'function' ? item.meta.title(route) : item.meta?.title }
    })).filter(item => !item.meta.hideInMenu);
    return [{ ...homeItem, to: homeRoute.path }, ...res];
};
export const getRouteTitleHandled = (route) => ({
    ...route,
    meta: { ...route.meta, title: typeof route.meta?.title === 'function' ? route.meta.title(route) : route.meta?.title }
});

export const showTitle = (item, vm) => item.meta?.title || item.name;

/**
 * @description 本地存储和获取标签导航列表
 */
export const setTagNavListInLocalstorage = list => {
    localStorage.tagNaveList = JSON.stringify(list);
};
/**
 * @returns {Array} 其中的每个元素只包含路由原信息中的name, path, meta三项
 */
export const getTagNavListFromLocalstorage = () => {
    const list = localStorage.tagNaveList;
    return list ? JSON.parse(list) : [];
};

/**
 * @param {Array} routers 路由列表数组
 * @description 用于找到路由列表中name为home的对象
 */
export const getHomeRoute = (routers, homeName = config.homeName) => {
    let i = -1;
    const len = routers.length;
    let homeRoute = {};
    while (++i < len) {
        const item = routers[i];
        if (item.children && item.children.length) {
            const res = getHomeRoute(item.children, homeName);
            if (res.name) return res;
        } else {
            if (item.name === homeName) homeRoute = item;
        }
    }
    return homeRoute;
};

/**
 * @param {*} list 现有标签导航列表
 * @param {*} newRoute 新添加的路由原信息对象
 * @description 如果该newRoute已经存在则不再添加
 */
export const getNewTagList = (list, newRoute) => {
    const { name, path, meta } = newRoute;
    const newList = [...list];
    if (newList.findIndex(item => item.name === name) >= 0 || meta?.notTag === true) return newList;
    else newList.push({ name, path, meta });
    return newList;
};

/**
 * @param {*} access 用户权限数组，如 ['super_admin', 'admin']
 * @param {*} route 路由列表
 */
const hasAccess = (access, route) => route.meta?.access ? hasOneOf(access, route.meta.access) : true;

/**
 * 权鉴
 * @param {*} name 即将跳转的路由name
 * @param {*} access 用户权限数组
 * @param {*} routes 路由列表
 * @description 用户是否可跳转到该页
 */
export const canTurnTo = (name, access, routes) => {
    const routePermissionJudge = (list) => list.some(item => {
        if (item.children && item.children.length) {
            return routePermissionJudge(item.children);
        } else if (item.name === name) {
            return hasAccess(access, item);
        }
    });

    return routePermissionJudge(routes);
};

/**
 * @param {Array} list 标签列表
 * @param {String} name 当前关闭的标签的name
 */
export const getNextRoute = (list, route) => {
    let res = {};
    if (list.length === 2) {
        res = getHomeRoute(list);
    } else {
        const index = list.findIndex(item => routeEqual(item, route));
        if (index === list.length - 1) res = list[list.length - 2];
        else res = list[index + 1];
    }
    return res;
};

/**
 * @param {Number} times 回调函数需要执行的次数
 * @param {Function} callback 回调函数
 */
export const doCustomTimes = (times, callback) => {
    let i = -1;
    while (++i < times) {
        callback(i);
    }
};

/**
 * @param {Object} file 从上传组件得到的文件对象
 * @returns {Promise} resolve参数是解析后的二维数组
 * @description 从Csv文件中解析出表格，解析成二维数组
 */
export const getArrayFromFile = (file) => {
    const nameSplit = file.name.split('.');
    const format = nameSplit[nameSplit.length - 1];
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsText(file); // 以文本格式读取
        let arr = [];
        reader.onload = function (evt) {
            const data = evt.target.result; // 读到的数据
            const pasteData = data.trim();
            arr = pasteData.split((/[\n\u0085\u2028\u2029]|\r\n?/g)).map(row => row.split('\t')).map(item => item[0].split(','));
            if (format === 'csv') resolve(arr);
            else reject(new Error('[Format Error]:你上传的不是Csv文件'));
        };
    });
};

/**
 * @param {Array} array 表格数据二维数组
 * @returns {Object} { columns, tableData }
 * @description 从二维数组中获取表头和表格数据，将第一行作为表头，用于在iView的表格中展示数据
 */
export const getTableDataFromArray = (array) => {
    let columns = [];
    let tableData = [];
    if (array.length > 1) {
        const titles = array.shift();
        columns = titles.map(item => ({
            title: item,
            key: item
        }));
        tableData = array.map(item => {
            const res = {};
            item.forEach((col, i) => {
                res[titles[i]] = col;
            });
            return res;
        });
    }
    return {
        columns,
        tableData
    };
};

export const findNodeUpper = (ele, tag) => {
    if (ele.parentNode) {
        if (ele.parentNode.tagName === tag.toUpperCase()) {
            return ele.parentNode;
        } else {
            return findNodeUpper(ele.parentNode, tag);
        }
    }
};

export const findNodeUpperByClasses = (ele, classes) => {
    const { parentNode } = ele;
    if (parentNode) {
        const { classList } = parentNode;
        if (classList && classes.every(className => classList.contains(className))) {
            return parentNode;
        } else {
            return findNodeUpperByClasses(parentNode, classes);
        }
    }
};

export const findNodeDownward = (ele, tag) => {
    const tagName = tag.toUpperCase();
    if (ele.childNodes.length) {
        let i = -1;
        const len = ele.childNodes.length;
        while (++i < len) {
            const child = ele.childNodes[i];
            if (child.tagName === tagName) return child;
            else return findNodeDownward(child, tag);
        }
    }
};

export const showByAccess = (access, canViewAccess) => hasOneOf(canViewAccess, access);

/**
 * @description 根据name/params/query判断两个路由对象是否相等
 * @param {*} route1 路由对象
 * @param {*} route2 路由对象
 */
export const routeEqual = (route1, route2) => {
    const params1 = route1.params || {};
    const params2 = route2.params || {};
    const query1 = route1.query || {};
    const query2 = route2.query || {};
    return (route1.name === route2.name) && objEqual(params1, params2) && objEqual(query1, query2);
};

/**
 * 判断打开的标签列表里是否已存在这个新添加的路由对象
 */
export const routeHasExist = (tagNavList, routeItem) => {
    const len = tagNavList.length;
    let res = false;
    doCustomTimes(len, (index) => {
        if (routeEqual(tagNavList[index], routeItem)) res = true;
    });
    return res;
};

export const localSave = (key, value) => {
    localStorage.setItem(key, value);
};

export const localRead = (key) => localStorage.getItem(key) || '';
